// @flow
import React from 'react';
import '../css/aboutus.css';

export default class PrivacyPolicy extends React.Component {
  render() {
    return (
      <div className="termsandconditions">
        <h1>Privacy Policy</h1>
        <h3 >1. Introduction</h3>
        <p >SimplyBook.me ltd, provides an appointment and scheduling system, that has many additional features such as promotion and marketing system, sales system and client contact system.</p>
        <p >If you do not agree with this policy, do not access or use our Services or interact with any other aspect of our business.</p>
        <p >1.1 We are committed to safeguarding the privacy of our website visitors and our system users who register as users of our system. See Data Processing Agreement for details about our relationship as Processor and Controller concerning the data subjects of Controller.</p>
        <p >1.2 This policy applies where we are acting as a data controller with respect to the personal data of our website visitors and system users; in other words, where we determine the purposes and means of the processing of that personal data.</p>
        <p >1.3 We will ask you to consent to our use of cookies in accordance with the terms of this policy when you visit our website.</p>
        <p >1.4 Our website incorporates privacy controls which affect how we will process your personal data. Upon signing up as a system user, you will be asked for information about your business. You can at any point in time, go to the System Settings, and choose to delete the system, and then all your data and all your clients information will be deleted from our servers within 30 days.</p>

        <h3 >2. How we use your personal data</h3>
        <p >2.1 In this Section 2 we have set out:</p>
        <p >2.2 We may process data about your use of our website and system ("<span class="bold">usage data</span>"). The usage data may include your company information, your IP address, geographical location, browser type and version, operating system, referral source, length of visit, page views and website navigation paths, as well as information about the timing, frequency and pattern of your system use. The source of the usage data is are analytics tracking system such as Google Analytics and similar. This usage data may be processed to understand better users behaviour to help us make the system better and to communicate with our users with usage hints. The legal basis for this processing is our legitimate interests, namely monitoring and improving our website, system, client service and system services.</p>
        <p >2.3 We collect information about you when you register for an account, create or modify your profile, set preferences, sign-up for or make purchases through the System. For example, you provide your contact information when you register for the System. You also have the option of adding a profile photo, bio, and other details to your profile information to be displayed on your Service provider profile or on your Company profile with our system so that your clients can book services with you. We keep track of your preferences when you select settings. We may process your "<span class="bold">account data</span>". The account data may be processed for the purposes of operating our website, providing our services, ensuring the security of our website and services, maintaining back-ups of our databases and used for communicating with you. The legal basis for this processing is to be able to provide you with our system services so that you can display your information and sell services and products.</p>
        <p >2.4 We may process your information included in your company profile in our system. ("<span class="bold">company info</span>"). The profile data may include your name, address, telephone number, email address, profile pictures, and other details that you add to your profile. The profile data may be processed for the purposes of enabling your use of our website and services. This information is available on the users booking site, supplied by us, on a widget that may be inserted into own site or in Facebook. It may also be made available on our directory sites where all system users are displayed, unless they specifically opt out of being displayed there. The legal basis for this processing is to enable proper operation of the system and services.</p>

        <h3 >3. Providing your personal data to others</h3>
        <p >3.1 We may disclose your personal data to any member of our group of companies, this means our subsidiaries, full time work contractor staff, such as live help, our ultimate holding company and all its subsidiaries insofar as reasonably necessary for the purposes, and on the legal bases, set out in this policy</p>
        <p >3.2 We may disclose your personal data to our insurers and/or professional advisers insofar as reasonably necessary for the purposes of obtaining or maintaining insurance coverage, managing risks, obtaining professional advice, or the establishment, exercise or defence of legal claims, whether in court proceedings or in an administrative or out-of-court procedure.</p>
        <p >3.3 Financial transactions relating to our website and services are handled by our payment services providers, [Paymentwall, PayPal]. We will share transaction data with our payment services providers only to the extent necessary for the purposes of processing your payments, refunding such payments and dealing with complaints and queries relating to such payments and refunds.</p>
        <p >3.4 If you agreed on getting proposals from partners, we may disclose your enquiry data to one or more of those selected third party suppliers of goods and services enabling them to contact you so that they can offer, market and sell to you relevant goods and/or services. Each such third party will act as a data controller in relation to the enquiry data that we supply to it; and upon contacting you, each such third party will supply to you a copy of its own privacy policy, which will govern that third party's use of your personal data.</p>

      </div>
    );
  };
};